@use "../partials/" as vars;
@use "../mixins/" as mix;

.form-container {
	width: 100%;
	height: calc(100% - 25%);
	background-image: linear-gradient(
		60deg,
		hsl(0deg 0% 0%) 0%,
		hsl(10deg 100% 13%) 2%,
		hsl(24deg 100% 16%) 6%,
		hsl(31deg 100% 20%) 14%,
		hsl(35deg 100% 24%) 25%,
		hsl(38deg 100% 29%) 44%,
		hsl(40deg 100% 34%) 66%,
		hsl(41deg 100% 39%) 84%,
		hsl(42deg 100% 45%) 94%,
		hsl(43deg 100% 50%) 100%
	);

	display: flex;
	flex-direction: column;
	align-items: center;
	justify-items: center;
}

.name-tag-container {
	margin-top: 25px;
	width: 70%;
	height: 17rem;
	border-radius: 10px;
	z-index: 2;
	background-color: vars.$clr-body;
	display: flex;
	align-items: center;
	@include mix.media(690px) {
		width: 90%;
	}
}

.name-tag-wrapper {
	color: black;
	width: 100%;
	display: flex;
	flex-direction: column;
	text-align: center;
}

.name-tag-title {
	width: 100%;
	height: 20px;
	margin-bottom: 15px;
	color: vars.$clr-text;
	text-align: center;
	font-size: 24px;
}

.name-tag {
	margin: 0 auto;
	width: 70%;
	height: 8rem;
	background-color: vars.$clr-imgbkcolor;
	position: relative;
	z-index: -1;
	top: 1rem;
	padding: 1px;
	display: flex;

	justify-content: space-evenly;
	font-size: 18px;
	font-family: "Covered By Your Grace", cursive;
	@include mix.media(874px) {
		width: 95%;
	}

	@include mix.media(690px) {
		width: 90%;
	}
}
.name-tag > img {
	width: min-content;
	height: min-content;
	padding: 5px;
}

.name-tag > p {
	width: 50%;
	//align-items: center;
	//justify-items: center;
	margin: 15px 15px 0 0;
}

.form-wrapper {
	width: 100%;
	height: 900px;
	margin-top: 25px;

	display: grid;
	grid-template-columns: repeat(2, 1fr);

	@include mix.media(690px) {
		grid-template-columns: repeat(1, 1fr);
	}
}

.f-contact {
	grid-column: 1 / -1;
	width: 100%;
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	@include mix.media(690px) {
		grid-template-columns: repeat(1, 1fr);
	}
}
.form-item {
	grid-column: span 1;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-items: center;
	width: 100%;
	@include mix.media(690px) {
		grid-column: 1 / -1;
		justify-items: left;
	}
}

.f-label {
	width: 300px;
	padding: 5px;
	text-transform: uppercase;
	border-radius: 5px 5px 0 0;
	color: vars.$clr-lines;
}

.f-input {
	width: 300px;
	height: 30px;
	outline: none;
	appearance: none;
	border: none;
	background-color: vars.$clr-imgbkcolor;
	transition: 0.5s ease;
	border-radius: 4px;
	padding: 5px;
}

.f-input:focus,
textarea:focus,
.btn-contact:focus {
	border: 1px solid vars.$clr-body;
	background-color: vars.$clr-lightgrey;
}

.form-error {
	width: 200px;
	margin: 2px;
	padding: 0.2rem;
	color: vars.$clr-lines;
	display: inline-block;
}

#message {
	outline: none;
	appearance: none;
	border: none;
}

.messagearea {
	outline: none;
	appearance: none;
	border: none;
	resize: none;
	background-color: vars.$clr-imgbkcolor;
	transition: 0.5s ease;
	border-radius: 4px;
	@include mix.media(690px) {
		width: 120%;
	}
}

.btn-wrapper {
	align-self: center;
	justify-self: end;
	margin: 50px;
	padding: 25px;
}
.btn-contact {
	width: 175px;
	padding: 15px;
	text-transform: uppercase;
	border: 1px solid black;
	border-radius: 10px 0 10px 0;
	margin: 20px 0 20px 25px;
	background-color: vars.$clr-button;
	color: vars.$clr-text;
}
