@use "../partials/" as vars;
@use "../mixins/" as mix;

.head-container {
	grid-area: header;

	width: 100%;
	height: max-content;
	grid-area: header;
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	justify-items: center;
	align-items: center;
	margin-top: 75px;
	background-color: vars.$clr-body;
	@include mix.media(800px) {
		grid-template-columns: repeat(1, 1fr);
	}
}

.logo {
	grid-column: span 1;
	background-color: vars.$clr-pill;
	border-radius: 50%;
	padding: 5px;
	margin-top: 75px;

	@include mix.media(800px) {
		grid-column: 1;
	}

	@include mix.media(450px) {
		transform: scale(0.7);
	}
}

.logo-img {
	border-radius: 50%;
	padding: 5px;
	background: rgb(2, 0, 36);
	background: radial-gradient(
		circle,
		rgba(2, 0, 36, 1) 0%,
		rgba(24, 24, 25, 1) 35%,
		rgba(0, 212, 255, 1) 100%
	);
}

.text-logo {
	grid-column: span 2;
	justify-items: center;
	margin-top: 75px;
	color: vars.$clr-pill;
	@include mix.media(800px) {
		grid-column: 1;
		display: flex;
		flex-direction: column;
		align-items: center;
	}
}

.text-h1 {
	margin-bottom: 0.625rem;
	text-align: center;
	@include mix.media(800px) {
		text-align: center;
	}
}

.cta {
	width: max-content;
	height: max-content;
	margin-top: 2.5rem;
	padding: 15px;
	border: 2px solid silver;
	border-radius: 0 10px 0 10px;
	background-color: vars.$clr-button;
	font-size: 1.25rem;
	@include mix.media(800px) {
		grid-column: 1 / -1;
		//margin-left: 170px;
	}
}

.trust-head {
	grid-column: span 3;
	color: vars.$clr-pill;
	margin: 3.125rem 0 03.125rem 0;
	@include mix.media(800px) {
		grid-column: 1 / -1;
		width: 100%;
	}
}

.trust {
	justify-items: center;
	@include mix.media(800px) {
		grid-column: 1 / -1;
		width: 100%;
		padding: 10px;
		text-align: center;
	}
}
