@use "../partials/" as vars;

// for opening nav container .open1 .nav-container
@mixin btn-nav-con() {
	position: relative;
	top: 0;
	width: 80%;
	//height: 275px;
	z-index: 4;
	opacity: 1;
	//	transform: top translateY(175px);
	transform: translateX(80px);
	transition: transform 350ms, opacity 350ms ease;
	border-radius: 0 0 15px 15px;
	background-color: vars.$clr-nav-sm;
}
//for opening nav wrapper .open1 .nav-wrapper
@mixin btn-nav-wrap() {
	//width: 60%;
	transition: transform 350ms ease-in;
	border-radius: 0 0 15px 15px;
	flex-direction: column;
	position: absolute;
	top: 0;
	z-index: 4;
	background-color: vars.$clr-body;
}

// rotate  span lines .open1 .lines
@mixin rotate-line() {
	transform: rotate(405deg) translate(6px);
	transition: transform 250ms ease-in-out;
	background-color: red;
}

// rotate the line beofre span after click .open1 .lines::before
@mixin rotate-lines-before() {
	opacity: 0;
	transition: transform 250ms, opacity 250ms ease;
}

// rotate the line afeter span after click .open1 .lines::after
@mixin rotate-lines-after() {
	transform: rotate(450deg) translate(-6px);
	transition: transform 250ms ease-in-out;
	background-color: red;
}

// change button color on click .open1 .btn1

@mixin button1() {
	color: vars.$clr-pill;
	transition: transform color 350ms ease-in;
}

@mixin media($width) {
	@media screen and (max-width: $width) {
		@content;
	}
}
