@use "../partials/" as vars;
@use "../mixins/" as mix;

html {
	scroll-behavior: smooth;
}

body {
	width: 100vw;
	height: 100vh;
	position: relative;
	//margin: 0 auto;
	background-color: vars.$clr-body;
}

.page-container {
	width: 95vw;
	//height: 95vh;
	margin: 0 auto;
	position: relative;

	display: flex;
	flex-direction: column;
	align-items: center;
	align-content: center;

	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-template-areas:
		"nav nav nav nav "
		"header header header header"
		"main main main main"
		"footer footer footer footer";

	@include mix.media(1200px) {
		grid-template-columns: repeat(3, 1fr);
		grid-template-areas:
			"nav nav nav "
			"header header header"
			"main main main"
			"footer footer footer";
	}
	@include mix.media(900px) {
		grid-template-columns: repeat(2, 1fr);
		grid-template-areas:
			"nav nav "
			"header header"
			"main main"
			"footer footer";
	}

	@include mix.media(800px) {
		grid-template-columns: repeat(1, 1fr);
		grid-template-areas:
			"nav"
			"header"
			"main"
			"footer";
	}
}
