@use "../partials/" as vars;
@use "../mixins/" as mix;

.services-container {
	width: 100%;
	height: auto;
	padding-top: 30px;
	background-color: vars.$clr-body;

	display: grid;
	grid-template-columns: repeat(6, 1fr);
	//justify-items: center;

	@include mix.media(1200px) {
		grid-template-columns: repeat(4, 1fr);
	}
	@include mix.media(800px) {
		grid-template-columns: repeat(2, 1fr);
	}
}

.services-title {
	grid-column: 1 / -1;
	text-align: center;
	width: 100%;
	color: vars.$clr-pill;
	margin-bottom: 10px;

	@include mix.media(800px) {
		padding: 10px;
	}
}

.services-contract {
	grid-column: 3 / 5;
	background-color: vars.$clr-pill;
	width: 100%;
	height: 100%;
	border-radius: 4px;

	@include mix.media(1200px) {
		grid-column: 2 / 4;
	}
	@include mix.media(800px) {
		grid-column: 1 / -1;
		width: max-content;
		margin: 0 auto;
		padding: 5px;
	}
}

.services-sub-title {
	width: 100%;
	height: 100px;
	font-size: 18px;
	color: black;
	display: flex;

	align-items: center;
	justify-content: space-evenly;
	@include mix.media(800px) {
		margin: 8px;
		flex-direction: column;
		justify-content: center;
	}
}

.services-sub-title > p {
	font-size: 1.25rem;
	padding-left: 4px;
}

.contract {
	width: 55px;
	height: 55px;
	// padding-right: 15px;
}

.calibration-svg {
	grid-column: 2 / 6;

	margin-top: 20px;
	padding: 20px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	width: 100%;
	@include mix.media(1200px) {
		grid-column: 1 / -1;
	}
}

.calibration {
	place-items: center;
	width: 100px;
	height: 100px;
	padding: 20px;
}

.services-card {
	grid-column: span 2;
	display: flex;
	align-items: center;
	flex-direction: column;

	justify-items: center;
	width: 100%;
	@include mix.media(1200px) {
		grid-column: span 2;
	}
	@include mix.media(800px) {
		grid-column: span 1;
	}

	 @include mix.media(667px) {
		grid-column:  1 /-1;
		width: 75%;
		justify-self: center;

	}

}

.service-card1 {
	@include mix.media(1200px) {
		grid-column: 1 / -1;
		justify-self: center;
		width: 50%;
		margin-top: 40px;
	}

	@include mix.media(667px) {

		width: 75%;

	}
}

.services-card h3 {

	color: white;
	text-align: center;
	padding: 15px;
}

.services-ul, .li-item {
	display: block;

	@include mix.media(1200px) {
		flex-direction: column;
	}
}

.services-additional-title {
	grid-column: 1 / -1;
	width: 100%;
	color: white;
	padding: 30px;
	text-align: center;
}

.services-additional-title h3 {
	padding: 25px;
}

.services-ul-item {
	grid-column: span 3;
	display: flex;
	justify-self: center;
	flex-wrap: wrap;
	width: 80%;
	padding: 20px;
	border-radius: 5px;
	background-color: vars.$clr-pill;

	@include mix.media(1200px) {
		grid-column: span 2;
		//width: 300px;
	}

	@include mix.media(800px) {
		grid-column: span 2;
		display: block;
		margin-bottom: 10px;
	}
}

.services-sub-ul-item {
	margin-left: 25px;
}

.li-item {
	list-style-type: none;
	padding: 5px;
}

.services-end {
	grid-column: 1 / -1;
	color: white;
	text-align: center;
	margin-top: 15px;
	padding: 20px;
}

.bang-area {
	padding: 15px;
}

.bang {
	border-radius: 50%;
	padding: 5px;
}
