@use "../partials/" as vars;
@use "../mixins/" as mix;

.nav-container {
	grid-area: nav;
	width: 95vw;
	//	height: max-content;
	padding: 0.5rem;
	display: flex;
	position: fixed;
	top: 0;
	background-color: vars.$clr-nav;
	z-index: 4;

	@include mix.media(800px) {
		transform: translateY(-5000px);
	}
}

.nav-wrapper {
	width: 100%;
	display: flex;
	justify-content: space-evenly;
}

.li-items {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-left: 4px;
	padding: 0.8rem;
	list-style-type: none;
	@include mix.media(800px) {
		align-items: flex-start;
		//	color: white;
	}
}

.nav-items {
	text-decoration: none;
	color: vars.$clr-pill;
	padding: 0.5rem;
}

.svg-img {
	height: 24px;
	width: 24px;
}

a:focus {
	border: 1px solid vars.$clr-body;
	background-color: vars.$clr-lightgrey;
}
