@use "../partials/" as vars;
@use "../mixins/" as mix;

.foot {
	grid-area: footer;
	width: 95vw;
	height: auto;
	color: vars.$clr-text;
	background-color: black;

	display: grid;
	grid-template-columns: repeat(3, 1fr);

	/* @include mix.media(798px) {
		flex-wrap: nowrap;
		flex-direction: column;
	} */
}

.locale {
	grid-column: 1 /3;
	width: 100%;
	height: max-content;
	padding: 15px;
	//align-items: center;
	@include mix.media(871px) {
		grid-column: 1 / -1;
		display: flex;
		flex-direction: column;
		align-items: center;
	}
}

.locale h2 {
	margin-bottom: 5px;
	@include mix.media(871px) {
		text-align: center;
		margin-bottom: 15px;
	}
}

.hours-container {
	grid-column: 3 / 3;
	width: 100%;
	height: max-content;
	//padding: 15px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;

	text-align: center;

	@include mix.media(871px) {
		grid-column: 1 / -1;
		display: flex;
		flex-direction: column;
		align-items: center;
		margin-top: 35px;
	}
}

.hoo {
	padding: 15px;
}

.underline {
	@include mix.media(871px) {
		width: 120px;
		height: 2px;
		border: 2px solid white;
	}
}

.social {
	grid-column: 3;
	width: 100%;
	height: max-content;
	padding: 5px;
	margin-top: 45px;
	@include mix.media(871px) {
		grid-column: 1 / -1;
		display: flex;
		flex-direction: column;
		align-items: flex-end;
		justify-content: flex-end;
	}
}

.social > span {
	width: 120px;
	height: 2px;
	margin-right: 15px;
	border: 2px solid white;
}

.social h3 {
	padding: 5px;
	margin-right: 45px;
}

.f-aitem {
	text-decoration: none;
	color: vars.$clr-text;
	padding: 10px;
	margin-right: 45px;
}

.svg-img {
	padding-left: 15px;
}

.foot-bbb {
	grid-column: 1 /3;
	width: 100%;
	height: max-content;
	margin-left: 100px;
	margin-bottom: 50px;

	@include mix.media(576px) {
		grid-column: 1;
		margin-left: 20px;
		font-size: 8px;
		& a,
		p {
			font-size: 12px;
		}
	}
}

.f-li-items {
	list-style-type: none;
	display: block;
}

.cpright {
	width: 100%;
	//margin-bottom: 50px;
}

.foot-bbb-img {
	max-width: 252px;
	max-height: 200px;
}

.foot-bbb-content {
	@include mix.media(576px) {
		width: 100%;
	}
}
