@use "../partials/" as vars;
@use "../mixins/" as mix;

.main-section {
	width: 95vw;
	height: max-content;
	background-color: white;
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	justify-items: center;
	margin-bottom: 50px;

	@include mix.media(1200px) {
		grid-template-columns: repeat(2, 1fr);
	}

	@include mix.media(968px) {
		grid-template-columns: repeat(1, 1fr);
	}
}

.hero-title-head {
	grid-column: span 3;
	width: 100%;
	height: 50px;
	align-items: center;
	margin: 2.25rem 0 2.25rem 0;

	@include mix.media(1200px) {
		grid-column: span 2;
	}

	@include mix.media(968px) {
		grid-column: 1 / -1;
	}
}

.hero-title {
	padding: 15px;
	width: 100%;
	height: 35px;
	text-align: center;
	justify-items: center;
	align-items: center;
	text-transform: capitalize;
}

.hero-head {
	grid-column: span 2;
	width: 100%;
	@include mix.media(1200px) {
		grid-column: span 1;
		border-radius: 12px;
	}

	@include mix.media(968px) {
		grid-column: 1 / -1;
		width: 95%;
		margin-bottom: 15px;
	}

	@include mix.media(430px) {
		width: 99%;
	}
}
.hero-text {
	width: 100%;
	height: max-content;

	border-radius: 5px;
	padding: 15px;
	background-color: silver;

	@include mix.media(1200px) {
		border-radius: 12px;
		background-color: black;
		color: vars.$clr-text;
	}

	@include mix.media(430px) {
		padding: 10px;
	}
}

.hero-img-head {
	width: max-content;
	height: max-content;
	grid-column: span 1;
	margin-left: 25px;

	@include mix.media(1200px) {
		margin-left: 50px;
	}

	@include mix.media(968px) {
		grid-column: 1 /-1;
		display: flex;
		justify-content: center;
		margin-left: 0;
		//width: 100%;
		//height: auto;
	}
}

.hero-img {
	width: 100%;
	height: auto;

	border-radius: 8px;

	padding: 8px;
	background: rgb(2, 0, 36);
	background: radial-gradient(
		circle,
		rgba(2, 0, 36, 1) 0%,
		rgba(24, 24, 25, 1) 35%,
		rgba(0, 212, 255, 1) 100%
	);

	@include mix.media(1200px) {
		margin-top: 40px;
	}

	@include mix.media(968px) {
		margin-top: 25px;
	}

	/* @include mix.media(443px) {
		transform: scale(0.9);
	}
} */
}
