@use "../partials/" as vars;
@use "../mixins/" as mix;

.work {
	width: 100%;
	height: max-content;
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	justify-items: center;
	gap: 15px;
	background-color: vars.$clr-imgbkcolor;
	padding-bottom: 20px;
@include mix.media(968px){
	grid-template-columns: repeat(1, 1fr);
}

}

.work-list {
	grid-column: span 1;
	margin: 50px 0 50px 0;
	width: 100%;
	max-height: max-content;
//	background-color: blue;
	@include mix.media(968px){
		grid-column: 1 /-1;
		margin: 50px 0 10px 0;
		//width: 75%;
	}
}


.work-head {
	width: 100%;
	height: max-content;
	text-align: center;
	margin-bottom: 10px;
}

.work-title {
	width: 100%;
	height: max-content;
	text-align: center;
	border: 1px solid black;
	border-radius: 5px;
	padding: 10px;
	margin-bottom: 20px;
	background-color: vars.$clr-pill;
}

.cert {
	justify-items: center;
	max-width: max-content;
	max-height: max-content;
	padding: 13px 0 30px 0;
}

.price {
	max-width: max-content;
	max-height: max-content;
}

.work-content {
	//color: vars.$clr-text;
	//background-color: vars.$clr-pill;
	width: 100%;
	height: max-content;
	padding: 10px;
	border-radius: 10px;
}

.work-content p {
	padding: 15px;
}
