@use "../partials/" as vars;
@use "../mixins/" as mix;

.task {
	width: 100%;
	height: max-content;
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	justify-items: center;
	align-items: center;
	color: vars.$clr-pill;
	background-color: vars.$clr-body;

	@include mix.media(968px) {
		grid-template-columns: repeat(2, 1fr);
	}

	@include mix.media(890px) {
		grid-template-columns: repeat(1, 1fr);
	}
}

.task > span {
	margin-top: 15px;
	margin-bottom: 15px;
}

.task-title-head {
	width: 100%;
	height: 125px;
	grid-column: 1 / -1;
	text-align: center;
	text-transform: capitalize;
	padding: 25px;
}

.task-title {
	padding: 15px;
	text-transform: capitalize;
	width: 100%;
	@include mix.media(968px) {
		grid-column: 1 /-1;
	}
}

.task-head {
	grid-column: 1 / 3;
	width: 100%;
	height: auto;
	margin-left: 25px;

	@include mix.media(968px) {
		grid-column: 1;
	}

	@include mix.media(890px) {
		grid-column: 1/ -1;
		width: 50%;
		justify-self: center;
		margin: 0;
	}
}

.task-img {
	width: 100%;
	height: auto;
	border-radius: 12px;
	padding: 15px;
	margin-bottom: 15px;
	background-color: vars.$clr-nav;
}

.task-ul-wrapper {
	grid-column: span 2;
	width: 100%;
	display: flex;
	align-items: center;

	@include mix.media(968px) {
		grid-column: 2 / 3;
		width: 80%;
	}

	@include mix.media(890px) {
		grid-column: 1 / -1;
		width: 50%;
	}
}

.task-ul-wrapper1 {
	grid-column: 1 / 3;
	width: 100%;
	@include mix.media(968px) {
		grid-column: 1 /2;
		width: 80%;
	}

	@include mix.media(890px) {
		grid-column: 1 /-1;
		width: 100%;
	}
}

.task-li-wrapper1 {
	width: 100%;
	height: max-content;
	text-align: center;
}

.task-li-wrapper2 {
	width: 100%;
	height: max-content;
	text-align: center;
}

.task-li-items {
	width: 100%;
	list-style-type: none;
	padding: 8px;
}

.task-female-cop {
	grid-column: 3 /-1;
	margin-bottom: 25px;
	margin-right: 45px;
	@include mix.media(968px) {
		grid-column: 2;
	}

	@include mix.media(890px) {
		grid-column: 1 / -1;
		width: 55%;
		margin-right: 0;
		margin-bottom: 15px;
	}
}

.task-img-1 {
	width: 100%;
	height: auto;
	padding: 15px;
	margin-top: 15px;
	border-radius: 12px;
	background-color: vars.$clr-nav;
}
