@use "../partials/"as vars;
@use "../mixins/"as mix;

.card-section {
	width: 100%;
	height: max-content;
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	justify-items: center;
	gap: 10px;
	background-color: vars.$clr-nav;
	padding-bottom: 25px;

	@include mix.media(1031px) {
		grid-template-columns: repeat(3, 1fr);
	}

	@include mix.media(968px) {
		grid-template-columns: repeat(2, 1fr);
	}

	@include mix.media(801px) {
		grid-template-columns: repeat(1, 1fr);
	}

}

.card-section-title {
	grid-column: 1 / -1;
	width: 100%;
	height: 125px;
	//	text-align: center;
	margin: 20px 0 15px 0;
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: center;

	@include mix.media(889px) {
		width: 90%;

	}

}

.card-section-h3 {

	margin-top: 48px;
	font-size: large;

	@include mix.media(889px) {
		width: 80%;
		text-align: center;

	}
}

.card-section-body {
	grid-column: 1 / -1;
	width: 50%;
	height: auto;
	//	text-align: center;
	margin: 10px 0 30px 0;
	background-color: black;
	border-radius: 10px;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-content: center;

	@include mix.media(1131px) {
		width: 70%;
	}

	@include mix.media(968px) {
		width: 80%;

	}

	@include mix.media(730px) {
		justify-content: flex-start;
		width: auto;
	}

	@include mix.media(566px) {
		margin: 15px 15px;
	}

}

.card-body-ul {
	//color: white;
	width: auto;
	height: auto;
	list-style-type: none;
	text-transform: capitalize;
	padding: 5px;
	//	justify-items: center;

}

.card-body-ul>li {
	width: 100%;
	margin: 10px 0 10px 0;
	padding: 5px;
	color: white;
	// text-align: center;
}


.card-wrappers {
	margin: 25px;
	width: 90%;
	height: 100%;
	grid-column: span 1;

	@include mix.media(1031px) {
		grid-column: span 1;
		width: 100%;
	}

	@include mix.media(968px) {
		//width: auto;
		grid-column: 1 / -1;
		width: 60%;

		//	margin: 45px 45px;


	}

	@include mix.media(800px) {
		width: 95%;



	}


}


.cards {
	border-radius: 15px;

	width: 100%;
	height: auto;
	display: flex;
	flex-wrap: wrap;

	//	justify-content: center;

	background-color: vars.$clr-text;
	/*
	@include mix.media(968px) {
	margin: 15px 15px;
  height: auto;

	} */

}


.cards>h2 {
	width: 100%;
	height: 55px;
	padding-top: 15px;
	text-align: center;
	//	align-items: center;
	background-color: white;
	border-radius: 15px 15px 0 0;
}


.card {
	width: 100%;
	height: 100%;
	//	background-color: vars.$clr-body;
	border-radius: 0 0 15px 15px;
	display: flex;
	flex-direction: column;
	align-content: center;
	justify-content: center;
	//	text-align: center;
}

.card-text {
	width: 100%;

	padding: 10px;
	color: vars.$clr-text;
	background-color: black;
}

.card-img {
	width: 225px;
	height: 225px;
	border-radius: 0 0 15px 15px;
	padding: 20px;
	margin: 0 auto;
//	background-color: vars.$clr-aactive;
}

.img-card {
	background-color: white;
	width: 100%;
	height: auto;
	border-radius: 15px;


	// @include mix.media(968px){
	// 	width: 225px;
	// 	height: 225px;

	// }

}
